ul li {
  background-color: rgba(var(--nav-bg-alpha), .8);
}

ul li a {
  display: block;
  padding: 2rem;
  width: 100%;
  border: 2px solid transparent;
}

ul li a:hover {
  border: 2px solid rgba(0, 0, 0, .2);
}
.convert-to-white { filter: invert(70%) sepia(100%) saturate(0%) hue-rotate(166deg) brightness(113%) contrast(112%); }
